        currencyId: "",
        amount: "0",
        fiftyFiveTen: false,
        incentives: false,
        startYearId: "",
        endYearId: "",
        startMonthId: "",
        endMonthId: "",
        totalMonth: null,
        grConsent: false,
        flexyForce: false,
        comissionRate: "0",
        serviceCostPerPerson: "0",
        ehsAmount: "0",
        totalAmount: "0",
