  validations: {
    model: {
      customerId: {
        required,
        maxLength: maxLength(36),
      },
      departmentId: {
        required,
        maxLength: maxLength(36),
      },
      subDepartmentId: {
        maxLength: maxLength(36),
      },
      purchaserId: {
        required,
        maxLength: maxLength(36),
