        // this.editedItem.itemCustomerId = this.customerId
        this.detectCurrencyInput();
      }
      val || this.close();
    },
    dialogDelete(val) {
      val || this.closeDelete();
    },
  },
  computed: {
    headers() {
      return [
        {
          value: "groupName",
          sortable: false,
        },
        {
          text: this.$t("employees.customerShortName"),
          value: "customerShortName",
          sortable: false,
        },
        {
          text: this.$t("employees.name"),
          value: "name",
          sortable: false,
        },
        {
          value: "tckn",
          text: this.$t("employees.tckn"),
          sortable: false,
        },
