          value: "customerShortName",
          sortable: false,
        },
        {
          text: this.$t("employees.name"),
          value: "name",
          sortable: false,
        },
        {
          value: "tckn",
          text: this.$t("employees.tckn"),
          sortable: false,
        },
        {
          value: "pernetNumber",
          text: this.$t("employees.pernetNumber"),
          sortable: false,
        },
      ];
    },
    customerIdErrors() {
      const errors = [];
      const field = "customerId";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].required &&
        errors.push(this.$t("v.text.required"));
      !this.$v["model"][field].maxLength &&
        errors.push(this.$t("v.string.maxLength", { max: "36" }));
      return errors;
    },
    departmentIdErrors() {
      const errors = [];
      const field = "departmentId";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].required &&
