        errors.push(this.$t("v.string.maxLength", { max: "3" }));
      return errors;
    },
  },
  created() {
    this.initializePurchaseOrders();
    this.initializeSowEmployees();
    this.model.id = generateId(this);
    if (!this.model.currencyId) {
      this.model.currencyId = "TRY";
    }
  },
  mounted() {
    if (this.item == null) {
      return;
    }
    if (this.item.currencyId == "TRY") {
      this.money.suffix = " TL";
    }
    if (this.item.currencyId == "EUR") {
      this.money.suffix = " EUR";
    }
    if (this.item.currencyId == "USD") {
      this.money.suffix = " USD";
    }
  },
  methods: {
    clearSearchData() {
      this.employees = [];
    },
    // enterSelect() {
    //   console.error(this.selectedSelected)
    // },
    deleteSelectedItems() {
      let Self = this;
      if (
        Array.isArray(this.selectedSelected) &&
        this.selectedSelected.length > 0
      ) {
        this.selectedSelected.forEach(function (val) {
          Self.sowEmployees = Self.sowEmployees.filter(function (ele) {
            return ele.id !== val.id;
          });
        });
      }
      Self.model.sowEmployees = JSON.stringify(Self.sowEmployees);
    },
    async searchDb() {
      await this.searchSowEmployees(this.search);
    },
    initializeSowEmployees() {
      if (
        typeof this.item === "object" &&
        this.item !== null &&
        Object.prototype.hasOwnProperty.call(this.item, "sowEmployees")
      ) {
        this.sowEmployees = JSON.parse(this.item.sowEmployees);
        this.model.sowEmployees = this.item.sowEmployees;
      }
    },
    initializePurchaseOrders() {
      if (
        typeof this.item === "object" &&
        this.item !== null &&
        Object.prototype.hasOwnProperty.call(this.item, "purchaseOrders")
      ) {
        this.purchaseOrders = JSON.parse(this.item.purchaseOrders);
        this.model.purchaseOrders = this.item.purchaseOrders;
      }
    },
    async searchSowEmployees(q) {
      this.loadingEmployees = true;
      let url = "/employees/findAllBySearch";
      if (q) {
        url = "/employees/findAllBySearch?q=" + q;
      }
      let response = await this.$admin.http.get(url);
      this.employees = response.data.data;
      this.groupedEmployees = arrayGroupBy(this.employees, "customerShortName");
      this.loadingEmployees = false;
      if (
        typeof this.item === "object" &&
        this.item !== null &&
        Object.prototype.hasOwnProperty.call(this.item, "sowEmployees")
      ) {
        this.selectedEmployees = JSON.parse(this.item.sowEmployees);
        // this.model.sowEmployees = this.item.sowEmployees
        this.initializeParentCheckboxes();
      }
    },
    initializeParentCheckboxes() {
      let selectedGroupEmployees = arrayGroupBy(
        this.selectedEmployees,
        "customerShortName"
      );
      let Self = this;
      Object.keys(this.groupedEmployees).forEach(function (group) {
        let childrenCount = Self.groupedEmployees[group].length;
        if (
          typeof selectedGroupEmployees[group] != "undefined" &&
          selectedGroupEmployees[group].length == childrenCount
        ) {
          Self.parentChecked[group] = true;
        }
      });
    },
    filterText(value, search) {
      return (
        value != null &&
        search != null &&
        typeof value === "string" &&
        value.toString().toLocaleLowerCase().indexOf(search) !== -1
      );
    },
    toggle(isSelected, select, e, group) {
      select(!isSelected);
      let selectedChildrenCount = this.groupedEmployees[group].length;
      let selectedGroupCount = 1;
      this.selectedEmployees.forEach(function (val) {
        if (val.customerShortName == group) {
          if (isSelected) {
            --selectedGroupCount;
          } else {
            ++selectedGroupCount;
          }
        }
      });
      if (selectedGroupCount == selectedChildrenCount) {
        this.parentChecked[group] = true;
      } else {
        this.parentChecked[group] = false;
      }
      let Self = this;
      setTimeout(function () {
        Self.model.sowEmployees = JSON.stringify(Self.selectedEmployees);
      }, 1000);
    },
    toggleChildren(group, e) {
      const Self = this;
      if (e) {
        // mark child checkboxes
        let selectedIds = [];
        this.selectedEmployees.forEach(function (val) {
          selectedIds.push(val.employeeId);
        });
        if (typeof this.groupedEmployees[group] != "undefined") {
          this.groupedEmployees[group].forEach(function (gVal) {
            if (!selectedIds.includes(gVal.employeeId)) {
              Self.selectedEmployees.push(gVal);
            }
          });
          // toggleParent()
        }
      } else {
        // unmark child checkboxes
        if (typeof this.groupedEmployees[group] != "undefined") {
          this.groupedEmployees[group].forEach(function (gVal) {
            Self.selectedEmployees = Self.selectedEmployees.filter(
              (item) => item.employeeId != gVal.employeeId
            );
          });
          // toggleParent()
        }
      }
      this.model.sowEmployees = JSON.stringify(this.selectedEmployees);
    },
    addToSowEmployees() {
      let Self = this;
      if (
        Array.isArray(this.selectedEmployees) &&
        this.selectedEmployees.length > 0
      ) {
        this.selectedEmployees.forEach(function (val) {
          let doesExist = Self.sowEmployees.some(function (ele) {
            return ele.id === val.id;
          });
          // do not add same record two times
          //
          if (!doesExist) {
            Self.sowEmployees.push(val);
          }
        });
      }
      this.model.sowEmployees = JSON.stringify(this.sowEmployees);
    },
    detectCurrencyInput() {
      if (this.model.currencyId == "TRY") {
        this.money.suffix = " TL";
      }
      if (this.model.currencyId == "EUR") {
        this.money.suffix = " EUR";
      }
      if (this.model.currencyId == "USD") {
        this.money.suffix = " USD";
      }
    },
    detectTotalMonths() {
      if (
        this.model.startYearId &&
        this.model.startMonthId &&
        this.model.endYearId &&
        this.model.endMonthId
      ) {
        let startDate =
          String(this.model.startYearId) +
          "-" +
          String(this.model.startMonthId) +
          "-01";
        let endDate =
          String(this.model.endYearId) +
          "-" +
          String(this.model.endMonthId) +
          "-01";
        let totalMonth = findTotalMonths(startDate, endDate);
        this.model.totalMonth = String(totalMonth);
      }
    },
    deleteItem(item) {
      this.editedIndex = this.purchaseOrders.indexOf(item);
      this.dialogDelete = true;
    },
    deleteItemConfirm() {
      this.purchaseOrders.splice(this.editedIndex, 1);
      this.model.purchaseOrders = JSON.stringify(this.purchaseOrders);
      this.closeDelete();
    },
    close() {
      this.dialog = false;
      this.$nextTick(async () => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },
    closeDelete() {
      this.dialogDelete = false;
      this.$nextTick(async () => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },
    onValidate() {
      if (this.$v.$invalid) {
        this.tabs = 0;
      }
    },
    onSelect(selected) {
      this.selected = selected;
    },
    save() {
      let sowId = this.id ? this.id : this.uid;
      let Self = this;
      this.selected.forEach(function (val) {
        var doesExist = Self.purchaseOrders.some(function (ele) {
          return ele.purchaseOrderId === val.purchaseOrderId;
        });
        // do not add same record two times
        //
        if (!doesExist) {
          Self.purchaseOrders.push({
            sowId: sowId,
            customerShortName: val.customerShortName,
            purchaseOrderId: val.purchaseOrderId,
            customerOrderNo: val.customerOrderNo,
            purchaseOrderNo: val.purchaseOrderNo,
            startDate: val.startDate,
            endDate: val.endDate,
          });
        }
      });
      this.model.purchaseOrders = JSON.stringify(this.purchaseOrders);
      this.close();
    },
  },
};
</script>
