<template>
  <va-form :id="id" :item="item" v-model="model">
    <v-row>
      <v-col>
        <v-card>
          <v-card-text>
            <v-row>
              <v-col sm="3">
                <va-text-input
                  source="roleKey"
                  :error-messages="roleKeyErrors"
                ></va-text-input>
                <va-text-input
                  source="roleName"
                  :error-messages="roleNameErrors"
                ></va-text-input>
                <va-text-input
                  source="roleLevel"
                  :error-messages="roleLevelErrors"
                ></va-text-input>
              </v-col>
            </v-row>
            <v-row>
              <v-col sm="6">
                <h2 class="display-0 pb-4">
                  {{ $t("menu.permissions") }}
                </h2>
                <va-text-input
                  v-show="false"
                  :label="$t('roles.permissions')"
                  v-model="model.rolePermissions"
                  hide-details
                ></va-text-input>

                <v-data-table
                  v-model="selected"
                  item-key="route"
                  :headers="headers"
                  :items="permissions"
                  :items-per-page="30"
                  :footer-props="{
                    'items-per-page-options': [10, 20, 30, 40, 50, 100],
                  }"
                  group-by="moduleName"
                  :search="search"
                  :custom-filter="filterText"
                >
                  <template v-slot:top>
                    <v-text-field
                      v-model="search"
                      :label="$t('roles.q')"
                      class="mx-4"
                    ></v-text-field>
                  </template>
                  <template
                    v-slot:[`group.header`]="{
                      isOpen,
                      toggle,
                      group,
                      isMobile,
                    }"
                  >
                    <td
                      class="text-start"
                      :colspan="isMobile ? undefined : headers.length"
                    >
                      <div style="display: flex; justify: center">
                        <div style="max-width: 10%">
                          <v-btn class="ma-0" icon small @click="toggle">
                            <v-icon>{{ isOpen ? "$minus" : "$plus" }}</v-icon>
                          </v-btn>
                        </div>
                        <div>
                          <v-checkbox
                            v-model="parentChecked[group]"
                            :label="group"
                            style="margin-top: 0px"
                            @change="toggleChildren(group, $event, toggle)"
                            hide-details="true"
                          ></v-checkbox>
                        </div>
                      </div>
                    </td>
                  </template>
                  <template v-slot:item="{ item, isSelected, select }">
                    <tr
                      style="cursor: pointer"
                      :class="isSelected ? 'v-data-table__selected' : ''"
                      @click="
                        toggle(isSelected, select, $event, item.moduleName)
                      "
                    >
                      <td class="d-flex align-center">
                        <v-icon size="16" color="green" v-if="isSelected"
                          >mdi-check</v-icon
                        >
                        <div v-if="!isSelected" style="width: 25px">&nbsp;</div>
                      </td>
                      <td>{{ item.route }}</td>
                      <td>{{ item.method }}</td>
                    </tr>
                  </template>
                </v-data-table>
              </v-col>
            </v-row>
            <va-save-button></va-save-button>
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>
  </va-form>
</template>

<script>
/// https://stackoverflow.com/questions/54344033/vuelidate-validate-form-with-sub-components

import { generateId, arrayGroupBy } from "../../js/common";
import { required, maxLength, numeric } from "vuelidate/lib/validators";

export default {
  props: ["id", "item"],
  provide() {
    return {
      $v: this.$v,
    };
  },
  data() {
    return {
      rolePermissions: [],
      parentChecked: [],
      selected: [],
      permissions: [],
      groupedPerms: [],
      search: "",
      model: {
        id: null,
        roleKey: null,
        roleName: null,
        roleLevel: null,
      },
      showError: false,
      errorMessage: "",
    };
  },
  validations: {
    model: {
      roleKey: {
        required,
        maxLength: maxLength(60),
      },
      roleName: {
        required,
        maxLength: maxLength(100),
      },
      roleLevel: {
        required,
        numeric,
        maxLength: maxLength(2),
      },
    },
  },
  computed: {
    headers() {
      return [
        {
          value: "groupName",
          sortable: false,
        },
        {
          text: this.$t("permissions.moduleName"),
          value: "moduleName",
          sortable: false,
        },
        {
          value: "route",
          text: this.$t("permissions.route"),
          sortable: false,
        },
        {
          value: "method",
          text: this.$t("permissions.method"),
          sortable: false,
        },
      ];
    },
    roleKeyErrors() {
      const errors = [];
      const field = "roleKey";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].required &&
        errors.push(this.$t("v.text.required"));
      !this.$v["model"][field].maxLength &&
        errors.push(this.$t("v.string.maxLength", { max: "60" }));
      return errors;
    },
    roleNameErrors() {
      const errors = [];
      const field = "roleName";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].required &&
        errors.push(this.$t("v.text.required"));
      !this.$v["model"][field].maxLength &&
        errors.push(this.$t("v.string.maxLength", { max: "100" }));
      return errors;
    },
    roleLevelErrors() {
      const errors = [];
      const field = "roleLevel";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].required &&
        errors.push(this.$t("v.text.required"));
      !this.$v["model"][field].numeric &&
        errors.push(this.$t("v.number.numeric"));
      !this.$v["model"][field].maxLength &&
        errors.push(this.$t("v.string.maxLength", { max: "2" }));
      return errors;
    },
  },
  created() {
    this.model.id = generateId(this);
    this.initializeRolePermissions();
  },
  methods: {
    async initializeRolePermissions() {
      let response = await this.$admin.http.get("/permissions/findAll");
      this.permissions = response.data.data;
      this.groupedPerms = arrayGroupBy(this.permissions, "moduleName");
      if (
        typeof this.item === "object" &&
        this.item !== null &&
        Object.prototype.hasOwnProperty.call(this.item, "rolePermissions")
      ) {
        this.selected = JSON.parse(this.item.rolePermissions);
        this.model.rolePermissions = this.item.rolePermissions;
        this.initializeParentCheckboxes();
      }
    },
    initializeParentCheckboxes() {
      let selectedGroupPerms = arrayGroupBy(this.selected, "moduleName");
      let Self = this;
      Object.keys(this.groupedPerms).forEach(function (group) {
        let childrenCount = Self.groupedPerms[group].length;
        if (
          typeof selectedGroupPerms[group] != "undefined" &&
          selectedGroupPerms[group].length == childrenCount
        ) {
          Self.parentChecked[group] = true;
        }
      });
    },
    filterText(value, search) {
      return (
        value != null &&
        search != null &&
        typeof value === "string" &&
        value.toString().toLocaleLowerCase().indexOf(search) !== -1
      );
    },
    toggle(isSelected, select, e, group) {
      select(!isSelected);
      let selectedChildrenCount = this.groupedPerms[group].length;
      let selectedGroupCount = 1;
      this.selected.forEach(function (val) {
        if (val.moduleName == group) {
          if (isSelected) {
            --selectedGroupCount;
          } else {
            ++selectedGroupCount;
          }
        }
      });
      if (selectedGroupCount == selectedChildrenCount) {
        this.parentChecked[group] = true;
      } else {
        this.parentChecked[group] = false;
      }
      let Self = this;
      setTimeout(function () {
        Self.model.rolePermissions = JSON.stringify(Self.selected);
      }, 1000);
    },
    toggleChildren(group, e) {
      const Self = this;
      if (e) {
        // mark child checkboxes
        let selectedIds = [];
        this.selected.forEach(function (val) {
          selectedIds.push(val.permId);
        });
        if (typeof this.groupedPerms[group] != "undefined") {
          this.groupedPerms[group].forEach(function (gVal) {
            if (!selectedIds.includes(gVal.permId)) {
              Self.selected.push(gVal);
            }
          });
          // toggleParent()
        }
      } else {
        // unmark child checkboxes
        if (typeof this.groupedPerms[group] != "undefined") {
          this.groupedPerms[group].forEach(function (gVal) {
            Self.selected = Self.selected.filter(
              (item) => item.permId != gVal.permId
            );
          });
          // toggleParent()
        }
      }
      this.model.rolePermissions = JSON.stringify(this.selected);
    },
  },
};
</script>
